@import "./src/style.scss";

* {
    font-family: 'Helvetica', sans-serif;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    margin: 0;
    padding: 0;
    outline: none;
    border: none;
}

body {
    background-color: #f4f4f4;
}

.card__container {
    width: 1170px;
    padding: 75px;
    margin: 0 auto 50px;
    background: white;
    border-radius: 15px;

    &:last-child {
        margin: 0 auto;
    }

    & .card__header {
        display: flex;
        justify-content: center;
        margin-bottom: 50px;

        & .card__iconContainer {
            @include flexible();

            & .card__icon {
                margin-right: 35px;
                display: block;
                width: 80px;
            }
            & .profileCard__icon {
                width: 160px;
            }
        }
        & .card__nameContainer {
            max-width: 805px;
            padding-left: 35px;
            border-left: 2px solid $paragraph-black;
            @include flexible();
            width: fit-content;

            & .card__name {
            }
            & .card__title {
                word-wrap: break-word;
                @include h2-bold();
                margin-bottom: 10px;
            }
            & .profileCard__title {
                margin-bottom: 0;
            }

            & .card__cipher {
                @include h3-regular();
                color: rgba(44, 44, 44, 0.5);
            }
        }
    }

    & .card__content {
        display: grid;
        grid-template-columns: max-content 1fr;
        grid-gap: 70px;

        & .card__info {

            & .card__item {
                margin-bottom: 50px;

                &:last-child {
                    margin-bottom: 0;
                }

                & .item__title {
                    @include h3-bold();
                    margin-bottom: 25px;
                }
                & .item__text {
                    @include paragraph-regular();
                    margin-bottom: 17px;
                }
                & .card__exam {
                    @include flexible();
                    margin-bottom: 17px;

                    img {
                        display: block;
                        width: 24px;
                        margin-right: 20px;
                    }
                    & .item__text {
                        margin-bottom: 0;
                    }

                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
            & .profileCard__item {
                margin-bottom: 68px;
            }
        }


        & .card__profiles {
            width: 100%;
            position: relative;

            & .profile__title {
                @include h3-bold();
                margin-bottom: 25px;
            }

            & .profile__container {
                display: block;
                padding: 15px 20px;
                margin-bottom: 10px;
                width: 100%;
                background-color: $primary-color;
                @include flexible();
                justify-content: space-between;
                border-radius: 10px;
                transition: all 0.4s ease;

                & .profile__text {
                    @include paragraph-regular();
                    color: white;
                }
                & .profile__ico {
                    display: block;
                    margin-left: 5px;
                }

                &:last-child {
                    margin-bottom: 0;
                }

                @include button-actions();
            }

            & .profile__description {
                @include paragraph-regular();
                white-space: pre-wrap;
                margin-bottom: 25px;
            }

            & .profile__important {
                @include paragraph-regular();
                & span {
                    @include paragraph-bold();
                    color: $primary-color;
                }
            }

            & .card__button {
                cursor: pointer;
                position: absolute;
                left: 0;
                bottom: 0;
                display: block;
                @include paragraph-bold();
                color: white;
                padding: 15px 40px;
                border-radius: 15px;
                background-color: $primary-color;
                @include button-actions();
            }
        }
    }
}

.swiper-pagination {
    width: 100% !important;
    position: relative !important;
    bottom: auto !important;
    margin: 0 auto 50px;
    column-gap: 15px;
    @include flexible();
    justify-content: center;
    flex-wrap: wrap;
}

.swiper-pagination-bullet {
    cursor: pointer;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    opacity: 1;
    background-color: rgba(25, 107, 177, 0.5);;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
    background: $primary-color;
    width: 30px;
    height: 30px;
}

.swiper-pagination-bullet:only-child {
    display: flex !important;
}
